import React ,{ useState,useEffect } from 'react';
import "./styles.css";
import List from './List'
import Alert from './Alert'

const getLocalStorage = () => {
  let list = localStorage.getItem('list');
  if(list){
    return JSON.parse(localStorage.getItem('list'))
  }
  else {
    return []
  }
}

function App() {

const [name,setName] = useState('');
const [list,setList] = useState(getLocalStorage()); 
const [isEditing,setIsEditing] = useState(false); 
const [editId,setEditId] = useState(null); 
const [alert,setAlert] = useState({show:true ,msg:'',
type:''}); 

var id1=Date.now();
//console.log(id1);ddfdf

const deleteItem = (id) => {

  showAlert(true,'item removed','red');
  
  const newItem1=list.filter((item)=> (item.id !== id));
  console.log(id,list,newItem1);
  setList(newItem1);
  

}

const showAlert =(show:false,msg:'',type:'') => {

setAlert({show,type,msg});

}



const editItem = (id) =>{
  
  const specificItem = list.find((item) => item.id===id);
  //console.log(specificItem);
  setEditId(id);
  setIsEditing(true);
  setName(specificItem.title)
}

const clearAllItem = () => {
  showAlert(true,"All items removed",'red');
  setList([]);
}
const submitHandler = (e) => {
  e.preventDefault();

  if(name === ''){
    showAlert(true,"Please enter an item",'red');
    return;
  }

  if(name && isEditing)
  {
    setList(
      list.map((item) => {
        if (item.id === editId) {
          return { ...item, title: name };
        }
        return item;
      })
    );
    setName('');
    setEditId(null);
    setIsEditing(false);
    showAlert(true, 'value changed successfully', 'green');
  }

  else 

  {
    showAlert(true,"new Item Added",'green')
  var newItem = {id:id1,title:name}
  setList([...list,newItem]);
  setName('');
  }
};

useEffect(()=>{
  localStorage.setItem("list",JSON.stringify(list))},[list]
  )

  return (
  
          <div>
           
             <section className='section-center'>
               
          { alert.show && <Alert alert={alert} showAlert={showAlert} list={list} />}
            <h2>Grocery Item List </h2>
             <form type='action' onSubmit={submitHandler}>
              
             <input type="text" placeholder='e.g, apple' value={name} 
             onChange={(e)=>{setName(e.target.value)}} /> 
             <button type='submit'>
              {isEditing ? 'edit' : 'submit' }
             </button>
             <List item={list} deleteItem={deleteItem} editItem={editItem} />
             </form>
             
             <div className='grocery-container'>
            
             </div> {console.log(list11())}
           { (list.length>0) && (<button onClick={clearAllItem}>Clear All</button> )}
          
            </section>
        
              </div>
          
          );
}


export default App;