 
import React ,{ useState } from 'react';
import { FaEdit,FaTrash } from 'react-icons/fa';

const List = ({item})  => {


return (<>

      
		   {item.map((item)=>{
         const {id,title} = item;
         return (<h1 key={id}>
           <p>{title}  <button type='button'><FaEdit /></button>
           <button type='button'><FaTrash /></button>
           </p> 
          </h1>
          
          );

       })} 
	  	  
       

        </>
		)
}
 
export default List;